import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReceiptQuoteComponent } from './receipt-quote.component';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
const routes: Routes = [
  {
    path: '',
    component: ReceiptQuoteComponent
  },
  {
    path: 'add',
    loadChildren: () => import('./add/add.module').then(m => m.AddModule)
  },
  {
    path: 'detail',
    loadChildren: () => import('./detail/detail.module').then(m => m.DetailModule)
  },
  {
    path: 'export-cart',
    loadChildren: () => import('./invoices/add/add.module').then(m => m.AddModule)
  }
]

@NgModule({
  declarations: [ReceiptQuoteComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TranslateModule,
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule
  ]
})
export class ReceiptQuoteModule { }
